@import url(https://fonts.googleapis.com/css?family=Exo:100);
body {
  margin: 0;
  padding: 0;
  font-family: 'Gugi', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100%;
}
.battle-wrapper{
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    overflow: hidden;
}
#preventClicks{
  background-color: transparent;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 200;
}
#endGame{
  background-color: rgba(0,0,0,0.6);
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2000;
  justify-content: center;
  align-items: center;
}
#endGame h2{
  background: #000;
  color: white;
  text-align: center;
  padding: 0.8em;
  width: 100%;
}
.attacks-gui{
  text-align: center;
  padding: 0!important;
  Color: white;
  display: flex;
  position: absolute;
  right: 0;
  bottom: 0;
  width: Calc(100vw - 1em)!important;
  overflow: hidden;
  justify-content: space-between;
  margin: 0 ;
}
.attacks-gui h1{
  margin: auto 1em;
  width: 50%
}
.attacks{
  margin-top:5%;
  display: flex;
  flex-flow: row wrap;
  background: #f7f7f7;   
  padding: 1em 0; 
  margin: 0;
  font-size: 1.5em;
}
.attack{
  flex-basis: Calc(50% - 5.6em);
  margin: 0.2em  2em;
  padding: 0.4em;
  cursor: pointer;
  color: #222;
  -webkit-transition: .1s;
  transition: .1s
}
.attack:hover{
  -webkit-transform: scale(1.1,1.1);
          transform: scale(1.1,1.1);
  color:#000
}
.attack:hover::before{
  content: '\25B6';
  position: absolute;
  margin-left: -2em;
  padding-bottom: 2em;
}


/* Cool infinite background scrolling animation. https://codepen.io/kootoopas/pen/reyqg
 * Twitter: @kootoopas
 */
/* Exo thin font from Google. */
/* Background data (Original source: https://subtlepatterns.com/grid-me/) */
/* Animations */
@-webkit-keyframes bg-scrolling-reverse {
  100% {
    background-position: 50px 50px;
  }
}
@keyframes bg-scrolling-reverse {
  100% {
    background-position: 50px 50px;
  }
}
@-webkit-keyframes bg-scrolling {
  0% {
    background-position: 50px 50px;
  }
}
@keyframes bg-scrolling {
  0% {
    background-position: 50px 50px;
  }
}
/* Main styles */
body {
  /* img size is 50x50 */
  background: url("data:image/png;base64,") repeat 0 0;
  -webkit-animation: bg-scrolling-reverse .92s infinite;
  /* Safari 4+ */
  /* Fx 5+ */
  /* Opera 12+ */
  animation: bg-scrolling-reverse .92s infinite;
  /* IE 10+ */
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
}
.character-wrapper{
    flex:1 1;
    display:flex;
    justify-content: space-between;
    margin: 0 em;
}
.character{
    height: 100%;
    width: 70vh;
    position: relative;
}
.character *{
    max-width: 100%;
    position: absolute;
}
.plattform{
    bottom: -60%;
    left: 15%;
    z-index: -1;
}
.character-details, .attacks-gui{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: #444;
    border: 8px solid #222;
    border-radius: 8px;
    padding: 0.6em 2em;
}
.character-details{
    display: flex;
    flex-flow: column nowrap;
    z-index: 2;
}
.character-details div{
    display: flex;
    align-items: center;
    justify-content: center;
}
.character-details >div{
    justify-content: space-between;
    color: white;
}
.life{
    background: #f7f7f7;
    border-radius: 10px;
    border: 2px solid #f7f7f7;
    height: 20px;
    width: 20em;
    display: flex;
    justify-content: flex-start !important;
}
.life div{
    height: 90%;
    border-top-right-radius: inherit;
    border-bottom-right-radius: inherit;
    -webkit-transition: .3s;
    transition: .3s
}
.life .green{
    background: #00FF33;
}
.life .yellow{
    background: #FFDD33;
}
.life .orange{
    background: #FF3300;
}
.life::before{
    color:#222;
    content:'HP';
    background: inherit;
    border-radius: inherit;
    margin: 0 0.2em;
}
.life + span{
    color:white;
    margin-left: auto;
}
/*Sort directions*/
.enemy{
    flex-direction: row-reverse;
}
.enemy .character-details{
    margin-left: auto;
    margin-top: 2%;
}
.player .character-details{
    margin-right: auto;
}

.player{
    margin-bottom: 8em;
}


/**Special animation Effects**/
@-webkit-keyframes tilt{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes tilt{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.enemy .avatar-tilt{
    -webkit-animation: tilt 0.2s linear 1s infinite;
            animation: tilt 0.2s linear 1s infinite;
}
.player .avatar-tilt{
    -webkit-animation: tilt 0.2s linear 0s infinite;
            animation: tilt 0.2s linear 0s infinite;
}
